import { useState, useEffect, } from 'react';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container, Backdrop, CircularProgress } from '@mui/material';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import CreateRechargeCardPlan from '../../sections/admin/recharge_card_plan';
import axios from '../../utils/axios';


export default function NewRechargeCardPlan() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const [load, SetLoad] = useState(true)
  const { name = '' } = useParams();
  const isEdit = pathname.includes('edit');
  const [currentUser, SetCurrentUser] = useState();
  const AccessToken = window.localStorage.getItem('accessToken');
  useEffect(() => {
    if (name) {
      axios.post(`/api/habukhan/recharge_plan_card/${AccessToken}/secure`, {
        plan_id: name
      }).then(data => {
        SetCurrentUser(data.data?.plan);
        SetLoad(false)
      }).catch(() => SetLoad(false));
    } else {
      SetCurrentUser([]);
      SetLoad(false)
    }
  }, [AccessToken, name, SetCurrentUser]);
  return (
    <Page title={"Plan: " && !isEdit ? 'Add Recharge Card Plan' : 'Edit Recharge Card Plan'}>
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Create Recharge Card Plan' : `Edit Recarge Card => Network ${!currentUser?.network ? '' : currentUser?.network}`}
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'Recharge Card Plans', href: PATH_ADMIN.plan.recharge_card_plan },
            { name: !isEdit ? 'Network' : currentUser?.network },
          ]}
        />

        <CreateRechargeCardPlan isEdit={isEdit} currentUser={currentUser} />
        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={load}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </Container>
    </Page>
  );
}
